// two players
export enum PLAYER {
    Red,
    Black
};
export const opponent = function (self: PLAYER): PLAYER {
    return self == PLAYER.Black ? PLAYER.Red : PLAYER.Black;
}
// 7 types of pieces
export enum PIECE {
    King,
    Guard,
    Bishop,
    Knight,
    Rook,
    Cannon,
    Pawn
};

export const character = function (type: PIECE, owner: PLAYER): string {
    switch (type) {
        case PIECE.King: return owner == PLAYER.Red ? "" : "";
        case PIECE.Guard: return owner === PLAYER.Red ? "" : "";
        case PIECE.Bishop: return owner == PLAYER.Red ? "" : "";
        case PIECE.Knight: return owner == PLAYER.Red ? "" : "";
        case PIECE.Rook: return owner === PLAYER.Red ? "" : "";
        case PIECE.Cannon: return owner == PLAYER.Red ? "" : "";
        case PIECE.Pawn: return owner === PLAYER.Red ? "" : "";
    }
}

// board size 9*10
export class SIZE {
    public static readonly files = 9;
    public static readonly ranks = 10;
};

export const pos2idx = function (x: number, y: number): number {
    return y * SIZE.files + x;
}

export enum STATUS {
    NotEnded,
    Win,
    Lost,
    Draw,
    FlyCheck,
    Stalemate,
    Impossbile
}


